
.Home-item-icon {
  background-image: url("./images/icons@2x.png");
  background-size: 192px 224px;
  width: 30px;
  height: 30px;
  display: inline-block;
}
.Home-item-icon-keysignature {
  background-position: 0px 0;
}
.Home-item-icon-note {
  background-position: -32px 0;
}
.Home-item-icon-interval{
  background-position: -64px 0;
}
.Home-item-icon-scale {
  background-position: -96px 0;
}
.Home-item-icon-chord {
  background-position: -128px 0;
}

a {
  text-decoration: none;
  /*color: #3083fb;*/
  color: white;
}

.Home-button {
  margin: 8px;
  float: left;
  width: 25px;
  height: 25px;
  line-height:56px; 
  color: blue;
  text-align: center;
  /*background: #e0e0e0;*/
  border-radius: 50%;
  border: 1px solid white;
}

.Home-button-hi {
  width: 13px;
  height: 13px;
  background: white;
  margin: 6px;
  border-radius: 50%;
  /*box-shadow: 0 0 5px #909090;*/
}
.Home-icon {
  width: 160px;
  height: 56px;
  line-height: 56px;
  padding: 2px;
  margin: 0px;
  float: left;
  text-align: center;
  border-color: white;
  border-style: solid;
  border-width: 1px 0.5px 1px 0.5px;
}
.Home-icon-hi {
  /*box-shadow: 0 0 5px #3083fb;*/
  /*box-shadow: 0 0 5px #909090;*/
  background: rgba(255,255,255,0.4);
}



